.user-details-container {
  padding: 35px;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.user-details {
  display: flex;
  align-items: flex-start;
}

.user-name {
  padding-left: 20px;
}
.user-name p {
  color: #7e7e7e;
}
.edit-profile-btn {
  padding: 8px 10px;
  border: solid 1px #7e7e7e;
  border-radius: 2px;
  background-color: white;
  cursor: pointer;
  transition: 0.3s;
}
.edit-profile-btn:hover {
  background-color: #f5f9fc;
}
.edit-profile-title {
  padding: 20px 0px;
  border-bottom: solid 1px #dbd9d9;
}
.edit-profile-title-2 {
  color: grey;
  font-weight: 400;
}
.edit-profile-form {
  padding: 20px;
  border: solid 1px #dbd9d9;
  border-radius: 5px;
}
.edit-profile-form label h3 {
  margin: 0%;
  padding: 3px 0px;
}
.edit-profile-form label p {
  margin: 0%;
  padding: 3px 0px;
}
.edit-profile-form label input,
.edit-profile-form label textarea {
  padding: 5px;
  margin-bottom: 20px;
  border: solid 1px #dbd9d9;
  width: 100%;
  margin-top: 6px;
}

.user-submit-btn {
  padding: 14px 10px;
  background-color: #0a95ff;
  color: white;
  border: none;
  border-radius: 5px;
  transition: 0.2s;
  cursor: pointer;
}

.user-submit-btn:hover {
  background-color: #0074cc;
}

.user-cancel-btn {
  padding: 14px 10px;
  color: #0a95ff;
  background-color: transparent;
  border: none;
  margin-left: 10px;
  cursor: pointer;
}

.bio-con {
  margin-top: 1.5rem;
  width: 100%;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .user-details-container {
    flex-direction: column;
    gap: 1rem;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .edit-profile-form label input,
  .edit-profile-form label textarea {
    padding: 5px;
    margin-bottom: 20px;
    border: solid 1px #dbd9d9;
    width: 50%;
    margin-top: 6px;
  }
  .user-details-container {
    margin-top: 12px;
  }
}
