.chat-container {
  min-height: 90vh;
  margin: 20px 0;
  width: 100%;
}

/* * input section */
.chat-input-container {
  background-color: #f5f5f5;
  display: flex;
  justify-content: center;
  /* align-items: end; */
  padding: 1rem;
  border-radius: 8px;
  height: 100%;
  width: 100%;
  gap: 1rem;
}

.chatai-textarea {
  width: calc(80% - 36px);
  padding: 12px;
  resize: none;
  border-radius: 5px;
}

.chat-button {
  background: #ef8236;
  padding: 9px 24px;
  outline: none;
  border: none;
  border-radius: 5px;
  color: #fff;
  font-weight: 700;
  font-size: larger;
}

.copy-to-clipboard {
  margin: 12px 0;
}

.copy-to-clipboard button {
  padding: 6px 12px;
  border: #ef8236 1px solid;
  background-color: #fff;
  border-radius: 4px;
}